import React, {useState} from 'react';
import './App.css';
import bgImg from "./images/img.jpg"

function App() {
  const [isShow, setIsShow] = useState(false);
  const [msg, setMsg] = useState(["切水果","开始游戏"])
  let start = ()=>{
    setIsShow(true);
    const tk = setInterval(()=>{
      setIsShow(!isShow);
    },100)
    setTimeout(()=>{
      clearInterval(tk);
      setIsShow(false);
      if(Math.random() < 0.1){
        setMsg(["胜利！", "再接再厉"])
      }else{
        setMsg(["Game Over", "再来一次"])
      }
    },3000)
  }
  return (
    <div className="App">
      <header className="App-header" id = "main" style = {{display: isShow?"none":"flex"}}>
        <h1>{msg[0]}</h1>
        <button className = "App-start" onClick = {start}>{msg[1]}</button>
      </header>
      <img style = {{display: isShow?"block":"none",
        width: "100vw",
        height: "100vh",
      }} src = {bgImg} alt = "*" className = "App-bg">
        
      </img>
    </div>
  );
}

export default App;
